.header__toggle {
  display: flex;
  align-items: center;
}

/* Hide the native checkbox but keep accessibility */
.toggle-switch {
  position: absolute;
  opacity: 0;
  width: 0;
  height: 0;
}

/* Switch base */
.toggle-switch__label {
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 48px;
  height: 26px;
  cursor: pointer;
}

.toggle-switch__track {
  position: absolute;
  inset: 0;
  border-radius: 999px;
  background: color-mix(in srgb, var(--accent), transparent 80%);
  box-shadow: inset 0 0 0 1px color-mix(in srgb, var(--accent), transparent 50%);
}

.toggle-switch__thumb {
  position: absolute;
  left: 3px;
  top: 3px;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background: linear-gradient(to bottom, color-mix(in srgb, var(--accent), white 25%), var(--accent));
  box-shadow:
    0 2px 6px color-mix(in srgb, var(--accent), transparent 65%),
    inset 0 1px 0 rgba(255,255,255,0.5);
  transition: transform 0.18s ease, background 0.18s ease;
}

/* Checked state: brighter track and move thumb to right */
.toggle-switch:checked + .toggle-switch__label .toggle-switch__track {
  background: color-mix(in srgb, var(--accent), transparent 60%);
}

.toggle-switch:checked + .toggle-switch__label .toggle-switch__thumb {
  transform: translateX(22px);
  background: linear-gradient(to bottom, color-mix(in srgb, var(--accent), white 28%), color-mix(in srgb, var(--accent), transparent 6%));
}

@media (max-width: $phone) {
  .toggle-switch__label { width: 44px; height: 24px; }
  .toggle-switch__thumb { width: 18px; height: 18px; transform: translateX(0); }
  .toggle-switch:checked + .toggle-switch__label .toggle-switch__thumb { transform: translateX(20px); }
}
