.button-container {
  display: table;
  margin-left: auto;
  margin-right: auto;
}

button,
.button,
a.button {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 8px 18px;
  margin: 5px 0;
  text-decoration: none;
  text-align: center;
  border-radius: 8;
  border: 1px solid var(--accent);
  /* Glossy accent surface */
  background: linear-gradient(
    to bottom,
    color-mix(in srgb, var(--accent), white 22%),
    var(--accent)
  );
  color: var(--accent-contrast-color);
  font: inherit;
  font-weight: bold;
  appearance: none;
  cursor: pointer;
  outline: none;

  box-shadow:
    0 4px 12px color-mix(in srgb, var(--accent), transparent 75%),
    inset 0 1px 0 rgba(255,255,255,0.45);

  &:hover {
    background: linear-gradient(
      to bottom,
      color-mix(in srgb, var(--accent), white 28%),
      color-mix(in srgb, var(--accent), transparent 6%)
    );
    box-shadow:
      0 6px 16px color-mix(in srgb, var(--accent), transparent 65%),
      inset 0 1px 0 rgba(255,255,255,0.5);
  }

  /* variants */

  &.outline {
    background: transparent;
    box-shadow: none;
    padding: 8px 18px;

    :hover {
      transform: none;
      box-shadow: none;
    }
  }

  &.link {
    background: none;
    font-size: 1rem;
  }

  /* sizes */

  &.small {
    font-size: .8rem;
  }

  &.wide {
    min-width: 200px;
    padding: 14px 24px;
  }
}

a.read-more,
a.read-more:hover,
a.read-more:active {
  display: inline-flex;
  border: none;
  color: var(--accent);
  background: none;
  box-shadow: none;
  padding: 0;
  margin: 20px 0;
  max-width: 100%;
}

.code-toolbar {
  margin-bottom: 20px;

  .toolbar-item a {
    position: relative;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    padding: 3px 8px;
    margin-bottom: 5px;
    text-decoration: none;
    text-align: center;
    font-size: 13px;
    font-weight: 500;
    border-radius: 8px;
    border: 1px solid transparent;
    appearance: none;
    cursor: pointer;
    outline: none;
  }
}
