/* Runtime-selectable glossy themes via body classes */

body.glossy-dark {
  --accent-contrast-color: black;
  --article-link-color: var(inherit);
  /* Matte black background */
  --background: #0a0a0a;
  --border-color: rgba(255, 255, 255, 0.12);
  --color: #c9d1d9;
  --foreground: #c9d1d9;
  --menu-color: #c9d1d9;
  --syntax-func-color: color-mix(in srgb, var(--accent) 70%, #999 30%);
  --syntax-var-color: color-mix(in srgb, var(--accent) 90%, transparent);
  --syntax-punctuation-color: #e6edf3;
  --syntax-value-color: color-mix(in srgb, var(--accent), #e6edf3);
  --syntax-comment-color: rgba(255, 255, 255, 0.28);
  --syntax-line-highlight-mix: color-mix(in srgb, var(--accent) 90%, #888 10%);
  --syntax-line-highlight-background-color: hsla(210, 15%, 28%, 0.35);
  --syntax-line-highlight-color: #e6edf3;
  --syntax-line-highlight-box-shadow: rgba(255,255,255,0.6);
  --syntax-code-border-color: rgba(255, 255, 255, 0.12);
  --syntax-code-copy-button-background: hsla(0, 0%, 87.8%, 0.18);
  --syntax-code-copy-button-color: #bfc5d2;
  --syntax-code-copy-button-box-shadow-color: rgba(0, 0, 0, 0.25);
}

body.glossy-light {
  --accent-contrast-color: white;
  --article-link-color: var(inherit);
  /* Matte white background */
  --background: #fafafa;
  --border-color: rgba(0, 0, 0, 0.12);
  --color: #111213;
  --menu-color: #111213;
  --syntax-func-color: color-mix(in srgb, var(--accent) 70%, #000 30%);
  --syntax-var-color: color-mix(in srgb, var(--accent) 90%, #000);
  --syntax-punctuation-color: #1a1b1c;
  --syntax-value-color: color-mix(in srgb, var(--accent), #1a1b1c);
  --syntax-comment-color: rgba(0, 0, 0, 0.30);
  --syntax-line-highlight-mix: color-mix(in srgb, var(--accent) 90%, #000 10%);
  --syntax-line-highlight-background-color: hsla(210, 20%, 70%, 0.35);
  --syntax-line-highlight-color: hsl(210, 15%, 18%);
  --syntax-line-highlight-box-shadow: rgba(0,0,0,0.6);
  --syntax-code-border-color: rgba(0, 0, 0, 0.12);
  --syntax-code-copy-button-background: hsla(360, 0%, 87.8%, 0.18);
  --syntax-code-copy-button-color: #444;
  --syntax-code-copy-button-box-shadow-color: rgba(255, 255, 255, 0.25);
}
