/* Posts list page styling */
.posts-page {
  max-width: 1000px;
  margin: 0 auto;
  padding: 2rem 1rem;
}

.posts-title {
  text-align: center;
  color: var(--accent);
  margin-bottom: 2rem;
  font-size: 2.5rem;
}

.posts-controls {
  display: flex;
  gap: 1.5rem;
  margin-bottom: 2rem;
  align-items: center;
  flex-wrap: wrap;
  
  @media (max-width: $phone) {
    flex-direction: column;
    gap: 1rem;
  }
}

.search-bar {
  flex: 1;
  position: relative;
  min-width: 250px;
  
  input {
    width: 100%;
    padding: 0.8rem 2.5rem 0.8rem 1rem;
    background: rgba(var(--accent-rgb), 0.08);
    border: 1px solid rgba(var(--accent-rgb), 0.3);
    border-radius: 6px;
    color: var(--foreground);
    font-size: 0.95rem;
    transition: all 0.3s ease;
    
    &:focus {
      outline: none;
      border-color: var(--accent);
      background: rgba(var(--accent-rgb), 0.12);
    }
    
    &::placeholder {
      color: var(--foreground);
      opacity: 0.5;
    }
  }
  
  .search-icon {
    position: absolute;
    right: 1rem;
    top: 50%;
    transform: translateY(-50%);
    opacity: 0.6;
    pointer-events: none;
  }
}

.language-filter {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  
  label {
    color: var(--foreground);
    font-size: 0.9rem;
    font-weight: 500;
  }
  
  select {
    padding: 0.6rem 2rem 0.6rem 0.8rem;
    background: rgba(var(--accent-rgb), 0.08);
    border: 1px solid rgba(var(--accent-rgb), 0.3);
    border-radius: 6px;
    color: var(--foreground);
    font-size: 0.95rem;
    cursor: pointer;
    transition: all 0.3s ease;
    
    &:focus {
      outline: none;
      border-color: var(--accent);
      background: rgba(var(--accent-rgb), 0.12);
    }
    
    &:hover {
      border-color: var(--accent);
    }
  }
}

.category-filter {
  margin-bottom: 1.5rem;
  
  label {
    color: var(--foreground);
    font-size: 0.9rem;
    font-weight: 500;
    margin-right: 1rem;
  }
  
  .category-buttons {
    display: inline-flex;
    gap: 0.5rem;
    flex-wrap: wrap;
    margin-top: 0.5rem;
  }
  
  .category-btn {
    padding: 0.4rem 1rem;
    background: rgba(var(--accent-rgb), 0.08);
    border: 1px solid rgba(var(--accent-rgb), 0.3);
    border-radius: 20px;
    color: var(--foreground);
    font-size: 0.85rem;
    font-weight: 400;
    cursor: pointer;
    transition: all 0.2s ease;
    outline: none;
    
    &:hover {
      background: rgba(var(--accent-rgb), 0.15);
      border-color: var(--accent);
    }
    
    &:focus {
      outline: none;
    }
    
    &.active {
      background: var(--accent);
      border-color: var(--accent);
      color: var(--background);
      font-weight: 400;
    }
  }
}

.posts-list {
  display: grid;
  gap: 2rem;
}

.post-card {
  background: rgba(var(--accent-rgb), 0.03);
  border: 1px solid rgba(var(--accent-rgb), 0.2);
  border-radius: 8px;
  padding: 1.5rem;
  transition: all 0.3s ease;
  
  &:hover {
    border-color: var(--accent);
    background: rgba(var(--accent-rgb), 0.08);
    transform: translateY(-2px);
  }
}

.post-card-header {
  margin-bottom: 0.8rem;
}

.post-title {
  margin: 0;
  
  a {
    color: var(--accent);
    text-decoration: none;
    font-size: 1.5rem;
    transition: opacity 0.2s ease;
    
    &:hover {
      opacity: 0.8;
    }
  }
}

.post-meta {
  display: flex;
  gap: 1rem;
  align-items: center;
  margin-bottom: 1rem;
  flex-wrap: wrap;
  
  time {
    color: var(--foreground);
    opacity: 0.7;
    font-size: 0.9rem;
  }
  
  .post-categories {
    display: flex;
    gap: 0.5rem;
    flex-wrap: wrap;
  }
  
  .category-badge {
    padding: 0.2rem 0.6rem;
    background: rgba(var(--accent-rgb), 0.15);
    color: var(--accent);
    border-radius: 3px;
    font-size: 0.8rem;
    border: none;
  }
}

.post-summary {
  color: var(--foreground);
  line-height: 1.7;
  margin-bottom: 1rem;
  opacity: 0.9;
}

.post-tags {
  display: flex;
  gap: 0.5rem;
  flex-wrap: wrap;
  margin-bottom: 1rem;
  
  .tag {
    padding: 0.2rem 0.6rem;
    background: rgba(var(--accent-rgb), 0.1);
    color: var(--accent);
    border-radius: 3px;
    font-size: 0.75rem;
    border: 1px solid rgba(var(--accent-rgb), 0.2);
  }
}

.read-more {
  display: inline-block;
  color: var(--accent);
  text-decoration: none;
  font-weight: 500;
  font-size: 0.9rem;
  transition: all 0.2s ease;
  
  &:hover {
    opacity: 0.8;
    transform: translateX(4px);
  }
}

.no-results {
  text-align: center;
  padding: 3rem 1rem;
  color: var(--foreground);
  opacity: 0.7;
  
  p {
    font-size: 1.1rem;
  }
}

/* Light theme overrides - remove opacity that makes text appear dimmed */
body:not(.glossy-dark) {
  /* Force all text to solid black */
  .post-card,
  .post-title a,
  .post-meta time,
  .post-summary,
  .no-results,
  .search-bar input,
  .language-filter label,
  .language-filter select,
  .category-filter label,
  .category-btn {
    color: black !important;
    opacity: 1 !important;
  }
  
  .search-bar input::placeholder {
    color: rgba(0, 0, 0, 0.4) !important;
  }
  
  /* Remove blue tinted backgrounds that create foggy effect */
  .post-card {
    background: rgba(var(--accent-rgb), 0.03);
    border: 1px solid rgba(var(--accent-rgb), 0.2);
    
    &:hover {
      background: rgba(var(--accent-rgb), 0.08);
      border-color: var(--accent);
    }
  }
  
  .search-bar input {
    background: white;
    border: 1px solid rgba(0, 0, 0, 0.2);
    
    &:focus {
      background: white;
      border-color: var(--accent);
    }
  }
  
  .language-filter select {
    background: white;
    border: 1px solid rgba(0, 0, 0, 0.2);
  }
  
  .category-btn {
    background: white;
    border: 1px solid rgba(0, 0, 0, 0.2);
    
    &:hover {
      background: rgba(0, 0, 0, 0.04);
      border-color: var(--accent);
    }
    
    &.active {
      background: var(--accent);
      border-color: var(--accent);
      color: white !important;
    }
  }
  
  .category-badge,
  .post-tags .tag {
    background: rgba(var(--accent-rgb), 0.12);
    border-color: rgba(var(--accent-rgb), 0.3);
  }
}
