.profile-photo {
  display: block;
  width: 200px;
  height: 200px;
  border-radius: 50% !important;
  object-fit: cover;
  object-position: 40% center;
  border: 3px solid var(--accent);
  box-shadow: 0 0 20px rgba(var(--accent-rgb), 0.6), 0 0 40px rgba(var(--accent-rgb), 0.3);
  margin: 0 auto 30px !important;
  padding: 0 !important;
  background: transparent;
  
  @media (max-width: $phone) {
    width: 150px;
    height: 150px;
  }
}

figure:has(.profile-photo) {
  margin: 40px auto;
  text-align: center;
  
  & > .profile-photo {
    margin: 0 auto 30px !important;
  }
}
/* Post count under profile photo */
.post-count-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: -15px;
  margin-bottom: 20px;
  
  .post-count-label {
    font-size: 0.85rem;
    color: var(--foreground);
    opacity: 0.7;
    text-transform: uppercase;
    letter-spacing: 0.1em;
    margin-bottom: 0.3rem;
  }
  
  .post-count-number {
    display: inline-block;
    padding: 0.4rem 1.2rem;
    background: linear-gradient(135deg, rgba(var(--accent-rgb), 0.2), rgba(var(--accent-rgb), 0.1));
    color: var(--accent);
    border-radius: 20px;
    font-weight: 700;
    font-size: 1.5rem;
    border: 1px solid rgba(var(--accent-rgb), 0.4);
    box-shadow: 0 2px 8px rgba(var(--accent-rgb), 0.2);
    transition: all 0.3s ease;
    min-width: 3rem;
    text-align: center;
    
    &:hover {
      transform: scale(1.05);
      box-shadow: 0 4px 12px rgba(var(--accent-rgb), 0.3);
    }
  }
}